import { Command, Args, Config, Flags } from "@oclif/core";
import { runUlldApplication } from "../run/main.js";

export default class RunUlldApp extends Command {
    static state: "beta";
    static summary = "Run a ULLD application previously created by the ULLD cli";
    static description = "Run a ULLD application.";
    static help = `This command runs a ULLD app that was previously generated by the @ulld/build cli.`;
    static strict = false;
    static aliases: string[] = [
        "start"
    ]
    static flags = {} 
    static args = {};

    // static examples = [`$ ulld-dev append-router ./some/path/to/router.ts`];
    // static usage = "ulld-dev append-router <Path-to-router>";

    constructor(argv: string[], config: Config) {
        super(argv, config);
        // this.targetRouterPath = path.join(
        //     process.cwd(),
        //     "node_modules/@ulld/api/src/trpc/main.ts",
        // );
    }

    async run(): Promise<void> {
        const { args, flags } = await this.parse(RunUlldApp);
        await runUlldApplication()
    }
}
